@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    @font-face {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-display: swap;
        src: url(./fonts/Roboto-Regular.ttf) format('truetype');
    }
}

@layer utilities {
    .__tmp_fix_select,
    .__tmp_fix_select option {
        color: #1A283D;
    }

    .__tmp_fix_select:invalid,
    .__tmp_fix_select option[value=""] {
        color: #C8CCD0;
    }

    .react-flow__node.selected .node-content {
        border-color: #0077FF;
    }

    .react-flow__node-special.selected .node-content,
    .react-flow__edge.selected .edge-content {
        background-color: #0077FF;
    }

    .react-flow__handle {
        border: none;
        background-color: transparent;
        border-radius: 0;
    }

    .react-flow__handle::before {
        content: '';
        border-left: 1px solid transparent;
        display: block;
        height: 100%;
    }
}
